.App {
  display: flex;
  flex-direction: column;
  min-width: 550px;
  height: 100%;
  min-height: 100%;
}

.right {
  margin-left: auto;
}

h1, h2, h3 {
  font-family: 'Montserrat', Arial, sans-serif;
  text-align: center;
}

a {
  color: #65BAA9;
  text-decoration: none;
  transition: all 0.5s;
}

a:hover {
  color: #FAEAFF;
  transition: all 0.5s;
}

p {
  font-family: 'Montserrat', Arial, sans-serif;
  color: #FAEAFF;
  font-size: 1em;
  line-height: 1.5em;
  margin-bottom: 15px;
}

/* ---- Nav ---- */

nav {
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  padding-left: 20px;
  background-color: #262429;
  border-bottom: solid 5px #865FD4; 
}

nav ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: auto;
}

nav ul li {
  list-style: none; 
}

nav ul li a {
  font-family: 'Montserrat', Arial, sans-serif;
  display: flex;
  align-items: center;
  height: 50px;
  padding: 0px 20px;
  font-size: 1.2em;
}

.current {
  background-color: #865FD4;
  color: #FAEAFF;
}

/* ---- Banner ---- */

#banner {
  height: 300px;
  min-height: 300px;
  background: linear-gradient(45deg, #865FD4, #65BAA9 );
  border-top: solid 5px #1D1A20;
  border-bottom: solid 5px #1D1A20;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ---- Section ---- */

.container {
  border-top: solid 5px #865FD4;
  padding: 25px;
  flex: 1 1 auto;
}

h1 {
  color: #865FD4;
  font-size: 36px;
  width: 80%;
  padding-bottom: 10px;
  margin: 25px auto 45px auto;
}

/* ---- Article ---- */

.link-container {
  padding: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

h2 {
  margin-bottom: 10px;
  color: #B6A0D5;
  display: flex;
  align-items: center;
  justify-content: center;
}

 svg {
  margin: 0 5px;
}

article {
  width: 30%;
 }

article p {
  background: #262429;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

/* ---- Footer ---- */

footer {
  background: #262429;
  border-top: solid 5px #865FD4;
  justify-content: center;
  align-items: center;
  padding: 25px 50px;
  display: flex;
  flex-direction: row;
  color: #865FD4;
  height: 100px;
}

.desc {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  font-family: 'Montserrat', Arial, sans-serif;
  white-space: pre-wrap;

}

.desc a {
  display: flex;
  align-items: center;
}

.space {
  margin: 0px 5px;
}

/* ---- Contact Form ---- */

.contact-form {
  background: #262429;
  display: flex;
  flex-direction: column;
  width: 400px;
  margin: auto;
  padding: 25px;
  border-radius: 10px;
  font-family: 'Montserrat', Arial, sans-serif;
}

.contact-form input, .contact-form textarea {
  margin: 5px 0px 15px 0px;
  padding: 8px;
  background: #1D1A20;
  border: solid 2px #865FD4;
  color: #FAEAFF;
  border-radius: 5px;
}

.contact-form textarea {
  height: 150px;
  resize: none;
}

.contact-form label {
  color: #865FD4;
  font-weight: bold;
}

.contact-form p {
  padding: 5px;
  color: #65BAA9;
  margin-top: 25px;
  text-align: center;
}

p.error { 
  color: #ff4d4d;
  }

button {
  padding: 10px;
  background: #865FD4;
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
  font-weight: bold;
}